.Table {
  border-collapse: initial;
  border-spacing: 1;

  display: grid;
  grid-gap: 1px;
  grid-template-columns: auto 1fr 1fr 1fr 1fr 2fr 2fr;

  thead, tbody, tr {
    display: contents;
  }

  td, th {
    display: flex;
    min-height: 48px;
    justify-content: center;
    align-items: center;
    padding: 8px 18px;
  }

  th {
    color: white;
    font-size: 13px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.23;
    letter-spacing: -0.41px;

    background: #ef3e3e;
  }

  td {
    align-items: flex-start;
    padding: 22px 18px;

    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: -0.5px;

    box-shadow: 0 0 0 1px #f3f3f4;

    dt:after {
      content: ':';
    }

    dd {
      text-align: right;
    }

    &:nth-child(1) {

      div {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
          width: 32px;
          height: 32px;
          margin-right: 8px;
          border-radius: 4px;
        }

        span {
          flex-grow: 2;
        }
      }
    }

    dl {
      display: grid;
      grid-gap: 6px;
      grid-template-columns: 1fr 1fr;
      width: 100%;
    }
  }
}