@font-face {
  font-weight: 500;
  font-family: Gordita;
  font-style: normal;
  src:
    local('Gordita Medium'),
    local('Gordita-Medium'),
    url('./Gordita-Medium.woff2') format('woff2'),
    url('./Gordita-Medium.woff') format('woff');
  font-display: swap;
}

@font-face {
  font-weight: bold;
  font-family: Gordita;
  font-style: normal;
  src:
    local('Gordita Bold'),
    local('Gordita-Bold'),
    url('./Gordita-Bold.woff2') format('woff2'),
    url('./Gordita-Bold.woff') format('woff');
  font-display: swap;
}

@font-face {
  font-weight: normal;
  font-family: Gordita;
  font-style: normal;
  src:
    local('Gordita Regular'),
    local('Gordita-Regular'),
    url('./Gordita-Regular.woff2') format('woff2'),
    url('./Gordita-Regular.woff') format('woff');
  font-display: swap;
}

